import { Link, NavLink, useNavigate } from "react-router-dom";
import { Constants, removeToken } from "../Constants";
import { userStore } from "../Store";
const LeftBar = () => {

  const navigation = useNavigate();
  const setUser = userStore((state) => state.setUser)
  const handleLogout = (e) => {
    e.preventDefault()
    setUser({})
    // document.cookie = `_token=''; expires=Thu, 01 Jan 1970 00:00:00 UTC; path="/";max-age=0;`;
    removeToken('_token');
    navigation('/login');
  }

  const user = userStore((state) => state.user)
  return (
    <div className="col-lg-2 ps-lg-0">
      <div className="left-sidebar bg-white shadow h-100">
        <div className="leftprofile-block">

          {
            ((Object.keys(user).length)) ? 

            <div className="profiletoggle">
            <Link className="dropdown-toggle position-relative" role="button" id="dropdownMenuLink" data-bs-toggle="dropdown" aria-expanded="false">
                <img src={`${Constants.base}public/uploads/${user?.photo}`} alt={`${user?.photo}`} crossOrigin="anonymous" /> {user?.first_name || <span className="skeleton__name"></span>}
            </Link>
            <ul className="dropdown-menu" aria-labelledby="dropdownMenuLink">
              <li><Link className="dropdown-item" >Action</Link></li>
              <li><Link className="dropdown-item">Another action</Link></li>
            </ul>
          </div>

          : <></>

          }

        </div>
        <ul className="sidemenulist">
          <li><NavLink className={({ isActive }) => isActive ? "active" : ""} to="/moments"><span><img src="/assets/frontend/images/icons/icon3.png" /></span> Moments</NavLink></li>
          <li><NavLink className={({ isActive }) => isActive ? "active" : ""} to="/dashboard"><span><img src="/assets/frontend/images/icons/icon7.png" /></span> My Teams</NavLink></li>
          <li><Link className={({ isActive }) => isActive ? "active" : ""} onClick={handleLogout} ><span><img src="/assets/frontend/images/icons/icon7.png" /></span> Logout</Link></li>
        </ul>
      </div>
    </div>
  )
}
export default LeftBar;
